"use client";
import React from "react";
import {
  LayoutDashboard,
  Users,
  Calendar,
  Settings,
  Book,
  MessageSquare,
  ChevronLeft,
  ChevronRight,
  LogOut,
  Activity,
} from "lucide-react";
import { cn } from "@/lib/utils";
import { useSidebar } from "@/contexts/SidebarContext";

type NavItem = {
  icon: React.ElementType;
  label: string;
  href: string;
  active?: boolean;
};

const navItems: NavItem[] = [
  { icon: LayoutDashboard, label: "Dashboard", href: "#", active: true },
  { icon: Users, label: "Students", href: "#" },
  { icon: Calendar, label: "Schedule", href: "#" },
  { icon: Book, label: "Courses", href: "#" },
  { icon: MessageSquare, label: "Messages", href: "#" },
  { icon: Activity, label: "Analytics", href: "#" },
  { icon: Settings, label: "Settings", href: "#" },
];

const Sidebar = () => {
  const { collapsed, toggleSidebar } = useSidebar();

  return (
    <div
      className={cn(
        "fixed left-0 top-0 h-screen bg-sidebar border-r border-sidebar-border transition-all duration-300 ease-in-out z-50",
        collapsed ? "w-16" : "w-64"
      )}
    >
      {/* Logo and header */}
      <div className="flex items-center h-16 px-4">
        <div className="flex items-center overflow-hidden">
          <div
            className={cn(
              "flex items-center justify-center w-8 h-8 rounded-md bg-purple-600 text-white transition-all duration-300",
              collapsed ? "mr-0" : "mr-3"
            )}
          >
            <span className="font-bold text-lg">K</span>
          </div>
          <h1
            className={cn(
              "font-bold text-lg transition-opacity duration-200",
              collapsed ? "opacity-0 w-0" : "opacity-100"
            )}
          >
            Kidjig
          </h1>
        </div>
        <button
          onClick={toggleSidebar}
          className="ml-auto p-1.5 rounded-full hover:bg-sidebar-accent transition-colors duration-200"
          aria-label={collapsed ? "Expand sidebar" : "Collapse sidebar"}
        >
          {collapsed ? <ChevronRight size={18} /> : <ChevronLeft size={18} />}
        </button>
      </div>

      {/* Navigation */}
      <nav className="mt-4 px-2">
        <ul className="space-y-1">
          {navItems.map((item) => (
            <li key={item.label}>
              <a
                href={item.href}
                className={cn(
                  "flex items-center py-2.5 px-3 rounded-md text-sidebar-foreground transition-all duration-200 group relative overflow-hidden",
                  item.active
                    ? "bg-sidebar-accent font-medium text-sidebar-primary"
                    : "hover:bg-sidebar-accent"
                )}
              >
                <item.icon
                  size={20}
                  className={cn(
                    "transition-all duration-200",
                    item.active
                      ? "text-sidebar-primary"
                      : "text-sidebar-foreground group-hover:text-sidebar-primary",
                    collapsed ? "mx-auto" : "mr-3"
                  )}
                />
                <span
                  className={cn(
                    "whitespace-nowrap transition-all duration-200",
                    collapsed ? "w-0 opacity-0" : "w-auto opacity-100"
                  )}
                >
                  {item.label}
                </span>
                {item.active && (
                  <div
                    className={cn(
                      "absolute left-0 top-0 h-full w-1 bg-purple-600 rounded-r-md",
                      collapsed ? "opacity-0" : "opacity-100"
                    )}
                  />
                )}
              </a>
            </li>
          ))}
        </ul>
      </nav>

      {/* Footer */}
      <div
        className={cn(
          "absolute bottom-8 left-0 right-0 px-3",
          collapsed ? "flex justify-center" : ""
        )}
      >
        <button
          className={cn(
            "flex items-center py-2.5 px-3 rounded-md text-sidebar-foreground hover:bg-sidebar-accent w-full transition-all duration-200",
            collapsed ? "justify-center" : ""
          )}
        >
          <LogOut size={20} className={collapsed ? "mx-auto" : "mr-3"} />
          <span
            className={cn(
              "whitespace-nowrap transition-all duration-200",
              collapsed ? "w-0 opacity-0" : "w-auto opacity-100"
            )}
          >
            Logout
          </span>
        </button>
      </div>
    </div>
  );
};

export default Sidebar;
